<template>
  <div class="stay">
    <div>
      <el-button type="info" round @click="logout">logout</el-button>
    </div>
    <h1>待機室</h1>
    <br>
    現在(0){{this.pe}}人待ちです。
    <br>
  </div>
</template>

<script>
  import axios from "axios"

  export default {
    data() {
      return {
        username: this.$_getuserID(),
        pe:0
      }
    },
    async mounted(){
      if(this.$_getuserID == null){
        alert("ホーム画面から再度お試しください")
        this.$router.push('/home').catch(()=>{});
      }
      this.pe = await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ 'test@test.com' +'&num=1').data.i;
    },
    methods: { 
      async gasUpdate(){
        this.pe = await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ 'test@test.com' +'&num=1').data.i;
      },
      //timeout: function(){
      //  this.timeoutTimer = setTimeout(() => {
      //    this.gasUpdate();
      //    clearTimeout(this.timeoutTimer)
      //    }
      //    ,600000 //時間変更可能 600000
      //  )
      //},
      async logout(){
          await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ 'test@test.com' +'&num=2');
          this.$router.push('/home').catch(()=>{});
      }
    }
  }
</script>
